import React from 'react'
import {withRouter} from 'react-router-dom'
import './index.scss'

interface P {
    history: any,
    className?: string,
    hasBottom?: boolean
}

interface S {
}


class BackHome extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {className = '', hasBottom = false},
            state: {},
            goHome
        } = this
        return (
            <div className={`home_img ${className} ${hasBottom?'height':''}`} onClick={goHome}>
                <img src={'/lsc_h5/imgs/home1.png'} alt='' />
            </div>
        );
    }

    componentDidMount(): void {
    }

    goHome = () => {
        const {history} = this.props
        const mod = window.location.hash.split('/')[1]
        history.push({pathname: mod === 'museums' ? '/home/museum' : '/home/mall_home'})
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

export default withRouter(BackHome)
